@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;500;600;700&display=swap');
* {
  font-family: 'Montserrat', sans-serif;
}
body {
  display: flex;
  justify-content: center;
}

.app {
  width: 37.5rem;
}

h1  {
  font-size: 2.5rem;
  line-height: 1.05;
  text-align: left;
}

h2 {
  font-size: 3.5rem;
  line-height: 1.05;
  text-align: center;
}

h3 {
  margin: 0.625rem;
}

p {
  font-size: 1.125rem;
  line-height: 1.2;
}

.question-title {
  width: 37.5rem;
  height: 13.125rem;
  background-color: rgb(253, 178, 98);
  border-radius: 0.313rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(255,255,255)    
}

.questions-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 1rem 0 1.438rem 0;
}

.question-block {
  width: 17.875rem;
  height: auto;
  border-radius: 0.313rem;
  background-color: rgb(255,255,255);
  box-shadow: rgb(0,0,0,0.07) 0 0 0 0.063rem;
  text-align: center;
  margin-bottom: 0.938rem;
  padding: 0;
  overflow: hidden;
  border: none;
}

.question-block p {
  font-size: 0.688rem;
  font-style: italic;
}

.question-block a {
  color: rgb(153,153,153);
  text-decoration: none;
}
.answer-block {
  width: 37.5rem;
  background-color: rgb(81, 150, 203);
  border-radius: 0.313rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: rgb(255,255,255);
}

.answer-block img { 
  width: 90%;
}